'use strict';
//
var yaml = require('js-yaml');
var path = require('path');
var fs  = require('fs');
var React = require('react');
var ReactDOM = require('react-dom');
var AMUIReact = require('amazeui-react');
var style = require('./main.css');
var Topbar = AMUIReact.Topbar;
var CollapsibleNav = AMUIReact.CollapsibleNav;
var Nav = AMUIReact.Nav;
var NavItem = AMUIReact.NavItem;
var List = AMUIReact.List;
var ListItem = AMUIReact.ListItem;
var Grid = AMUIReact.Grid;
var Col = AMUIReact.Col;
var Article = AMUIReact.Article;
var ButtonToolbar = AMUIReact.ButtonToolbar;
var ButtonGroup = AMUIReact.ButtonGroup;
var Button = AMUIReact.Button;
var Icon = AMUIReact.Icon;
var Input = AMUIReact.Input;
var Form = AMUIReact.Form;

//
var config_path = path.join(__dirname,'_config.yml');
var config;
try {
    config = yaml.safeLoad(fs.readFileSync(config_path, 'utf8'));
} catch (e) {
    console.log(e);
}
console.log(config);
var Htopbar = React.createClass({
    render: function () {
        return( <Topbar brand="H-Editor" toggleNavKey="nav">
            <CollapsibleNav eventKey="nav">
                <Nav topbar>
                    <NavItem href="#"></NavItem>
                    <NavItem href="#"></NavItem>
                    <NavItem href="#"></NavItem>
                </Nav>
            </CollapsibleNav>
        </Topbar>
            );

    }
});
var HcategoryList = React.createClass({
    render: function () {
        return(
            <dl className='category_list'>
                <dt></dt>
                <dd>
                    <ul>
                        <li>
                            <Icon icon="book" />
                        
                            <span>(23)</span>
                        </li>
                        <li>
                            <Icon icon="book" />
                        
                            <span>(23)</span>
                        </li>
                    </ul>
                </dd>
            </dl>
            );
    }
});
var HpageList = React.createClass({
    render: function () {
        return(
            <dl className='page_list'>
                <dt>
                    <span className='page_total'> 24</span>
                </dt>
                <dd>
                    <ul>
                        <li>
                            <Icon icon="file-text-o" />
                        </li>
                        <li>
                            <Icon icon="file-text-o" />
                        </li>
                    </ul>
                </dd>
            </dl>
            );
    }

});
var Hbutton_group = React.createClass({
    render: function () {
        return(
            <ButtonToolbar>
                <Button>
                    <Icon icon="save" />
                
                </Button>
                <Button amStyle="warning">
                    <Icon icon="remove" />
                
                </Button>
            </ButtonToolbar>
            );
    }
});
var Hbutton_Toolbar = React.createClass({
    render: function () {
        return(
            <ul className="toolbar clearfix">
                <li className="tb-btn"><a title="" ><i className="fa fa-bold"></i></a></li>{/* bold */}
                <li className="tb-btn"><a title="" ><i className="fa fa-italic"></i></a></li>{/* italic */}
                <li className="tb-btn spliter"></li>
                <li className="tb-btn"><a title="" ><i className="fa fa-link"></i></a></li>{/* link */}
                <li className="tb-btn"><a title="" ><i className="fa fa-outdent"></i></a></li>{/* blockquote */}
                <li className="tb-btn"><a title=""><i className="fa fa-code"></i></a></li>{/* code */}
                <li className="tb-btn"><a title=""><i className="fa fa-picture-o"></i></a></li>{/* picture-o */}
                <li className="tb-btn spliter"></li>
                <li className="tb-btn"><a title=""><i className="fa fa-list-ol"></i></a></li>{/* list-ol */}
                <li className="tb-btn"><a title=""><i className="fa fa-list-ul"></i></a></li>{/* list-ul */}
                <li className="tb-btn"><a title=""><i className="fa fa-header"></i></a></li>{/* header */}
            </ul>
            );
    }
});
var HModebar = React.createClass({
    render: function () {
        return(
            <ul className="md-modebar">
                <li className="tb-btn pull-right">
                    <a className='preview'  title="">
                        <i className="fa fa-eye"></i>
                    </a>
                </li> { /* preview mode */ }
                <li className="tb-btn pull-right">
                    <a className='split'title="">
                        <i className="fa fa-columns"></i>
                    </a>
                </li> { /* split mode */ }
                <li className="tb-btn pull-right">
                    <a className='edit' title="">
                        <i className="fa fa-pencil"></i>
                    </a>
                </li> { /* edit mode */ }
                <li className="tb-btn spliter pull-right"></li>
                <li className="tb-btn pull-right"><a title="" ><i className="fa fa-arrows-alt"></i></a></li> {/* full-screen */}
            </ul>
            );
    }
});
var Harticle = React.createClass({
    render: function () {
        return(
            <div className='article_edit'>
                <dl>
                    <dt>
                        <Hbutton_group></Hbutton_group>
                    </dt>
                    <dd>
                        <Form className="am-form" target="_blank" horizontal>
                            <Input type="text" id="doc-ipt-1" placeholder="" />
                            <Input type="select" wrapperClassName="am-u-sm-12">
                                <option value=""></option>
                                <option value="v2"></option>
                                <option value="v3"></option>
                            </Input>

                            <div className="md-panel">
                                <div className="md-menubar">
                                    <Hbutton_Toolbar></Hbutton_Toolbar>
                                    <HModebar></HModebar>
                                </div>
                                <div className="md-editor">
                                    <textarea ref="editor" name="content"></textarea>{/* style={{height: this.state.editorHeight + 'px'}} */}
                                </div>
                                <div className="md-preview" ref="preview" ></div>
                                <div className="md-spliter"></div>
                            </div>
                        </Form>
                    </dd>
                    </dl>

            </div>
            );
    }
});

var Hroot = React.createClass({
    render: function () {
        return(
            <div>
                <Htopbar></Htopbar>
                <Grid className="content">

                    <Col sm={1} className="category_nav">
                        <HcategoryList/>
                    </Col>
                    <Col sm={2} className="page_nav">
                        <HpageList/>
                    </Col>
                    <Col sm={9} className="article_wrap">
                        <Harticle/>
                    </Col>
                </Grid>
            </div>
            );
    }
});

ReactDOM.render(
    <Hroot/>,
    document.getElementById('root')
);


